import React, { useRef, useEffect, useState } from "react";
import "./Home.css";

function Home() {
  const [entryList, setEntryList] = useState(
  [{id:0,category:"normal",date:"06-06-2021"}]
  );
  const [postList, setPostList] = useState(
  [{postId:0, contents:[{number:0,id:"0-0", firstName: "Postingan Pertama", lastName: "Test percobaan upload gambar animasi didalam web blogger",img:"https://i.pinimg.com/originals/49/26/52/492652d5649bb403be09e0ea3d04fbe7.gif" }]}] 
  );
  
  // handle input change
  const handleInputChange = (e, index1,index2) => {
  const { name, value } = e.target;
  const list = [...postList];
  list[index1].contents[index2][name] = value;
  setPostList(list);
};
 
// handle click event of the Remove button
  const handleRemoveClick1 = index => {
  

  const list = [...postList];
  //alert("container"+list[index]["id"])
  //const elem = document. getElementsByName("container"+list[index]["id"]);
  //elem. remove();
  
  
  list.splice(index, 1);
  setPostList(list);
};

const handleRemoveClick2 = (index1,index2) => {
  

  const list = [...postList];
  //alert("container"+list[index]["id"])
  //const elem = document. getElementsByName("container"+list[index]["id"]);
  //elem. remove();
  
  
  list[index1].contents.splice(index2, 1);
  setPostList(list);
};
 
// handle click event of the Add button
  const handleAddClick1 = () => {
     
    let lastId = Math.max.apply(null, postList.map(item => item.postId))
    let newId = lastId + 1;
    
    
    
   // setPostList([...postList, {postId:newId, contents:[{number:0,id:newId+"-"+0, firstName: "", lastName: "",img:null}]}]);
    
    const list = [...postList];
    list[0].contents.sort()
    list[0].contents.reverse()
   // list.sort((a, b) => a.postId > b.postId && 1 || -1)
  //  alert(JSON.stringify(list))
   
    setPostList(list)
    

};
const handleAddClick2 = (index,id) => {
     
    let lastSubId = Math.max.apply(null, postList[index].contents.map(item => item.number))
    let newSubId = lastSubId + 1;

    
    const list = [...postList];
    
    list[index].contents.unshift({number:newSubId,id:id+"-"+newSubId, firstName: "", lastName: "",img:null})
    
    setPostList(list)
    
    //setPostList([...postList, {id:newId, firstName: "Postingan Pertama", lastName: "Test percobaan upload gambar animasi didalam web blogger",img:"https://i.pinimg.com/originals/49/26/52/492652d5649bb403be09e0ea3d04fbe7.gif" }]);
};
  
  
    

    const handleImg = (e, index1,index2,id) => {
        const { name, value } = e.target;
        const list = [...postList];
        
        
       
        
        if (e.target.files[0] && e.target.name=="img2-"+id){
        	
            //alt[e.target.name] =e.target.files[0].name
            list[index1].contents[index2].img= URL.createObjectURL(e.target.files[0]);
         
           // document.getElementById("label2-"+id).style.width=document.getElementById("img2-"+id).clientWidth+5+"px"
          //  document.getElementById("label2-"+id).style.height=document.getElementById("img2-"+id).clientHeight+5+"px"
            setPostList(list);
            
            
         }
         //   setImg({
            
                
          //  });    
         
         
    };

  useEffect(() => {
 var modal = document.getElementById("myModal");
  // Get the button that opens the modal
  var btn = document.getElementById("myBtn");
  // Get the <span> element that closes the modal
  var span = document.getElementsByClassName("close")[0];

    // When the user clicks the button, open the modal 
  btn.onclick = function() {
  modal.style.display = "block";
  
}
// When the user clicks on <span> (x), close the modal
  span.onclick = function() {
  modal.style.display = "none";
}

// When the user clicks anywhere outside of the modal, close it
  window.onclick = function(event) {
  if (event.target == modal) {
    modal.style.display = "none";
  }
}
    })
  return (
    <div className="App">
      <button id="myBtn">Open Modal</button>
      <div className="row text1" >
        <div className="col1" >
         
          <p><b>Test</b></p>
          <p><a>Main page</a></p>
          <p><a>Recent changes</a></p>
          <p><a>Random page</a></p>
        
       </div>
  <div className="col2" >
  {postList.map((y, j) => {
        return (
        <div>
        
        {postList[j].contents.map((x, i) => {
        return (
          
          <div className="box" id="container" name={"container"+x.id}>
            <div className="btn-box">
          
          {postList[j].contents[i].id == postList[j].contents[0].id && <div className="add-entry2"><button onClick={()=>handleAddClick2(j,y.postId)}>Add</button></div>}
          </div>
            <div className="col2-title-box">
            
        <img src="https://www.pinclipart.com/picdir/big/531-5310418_for-print-free-images-yellow-folder-icon-png.png" className="form-img__img-folder"/>
        <div className="col2-category">
        <select>
        <option className="col2-option">category 1</option>
     <option className="col2-option">category 2</option>
     <option className="col2-option">category 3</option>
     <option className="col2-option">category 4</option>
        </select>
        </div>
            <input 
              className="col2-title"
              type="text"
              name="firstName"
   placeholder="Ketik Judul"
              value={x.firstName}
              onChange={e => handleInputChange(e, j, i)}
            />
            <label id={"label2-"+x.id} htmlFor={"photo"+x.id} className="form-img__file-label">
               
                    
                </label>
                
                
                <img src="https://pro.posterdigital.pt/wp-content/plugins/udraw/designer/includes/img/add_photo_placeholder.png" className="form-img__img-plus"/>
                
            </div>
            
            <form encType="multipart/form-data" id={x.id} >
     
            <div className="">
                <input 
                    name={"img2-"+x.id} 
                    type="file" 
                    accept=".png, .jpg, .jpeg" 
                    id={"photo"+x.id}
                    
                    
                    className="visually-hidden"
                    onChange={e => handleImg(e, j, i,x.id)}
                />
                <div className="img-preview">
                
                
                </div>
            </div>
        </form>
        <br/>
            ​<textarea className="col2-des" placeholder="Description" name="lastName" id="txtArea" rows="3" cols="70" value={x.lastName}
              onChange={e => handleInputChange(e, j, i)}></textarea>
              {postList[j].contents[i].img != null && <div>
            <center>
            <img id={"img2-"+x.id} src={x.img==null?"https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRFOk8MJZpq8LyDRRfsIeNqi5vWOFi1mjHSKA&usqp=CAU":x.img} className="form-img__img-preview landscape"  />
            </center>
            </div>}
            <div className="btn-box">
              {postList[j].contents.length !== 1 && <button
                className="mr10"
                onClick={() => handleRemoveClick2(j,i)}>Remove</button>}
              
            </div>
          </div>
          
        );
        
      })}
      <div className="btn-box">
              {postList.length !== 1 && <button
                className="mr10"
                onClick={() => handleRemoveClick1(j)}>Remove</button>}
              {postList.length - 1 === j && <div className="add-entry2"><button onClick={handleAddClick1}>Add</button></div>}
            </div>
      </div>
      );
      })}
      
      <div style={{ marginTop: 20 }}>{JSON.stringify(postList)}</div>
      
  </div>
  <div className="col3">
    <h4>What's New?</h4>
    <hr/>
      <div>
       <p><b>2021-05-31</b></p>
       <p>some text..</p>
      </div>
  </div>
</div>
 
     
      <div id="myModal" class="modal">
  
        <div class="modal-content">
          <span class="close">&times;</span>
          <p>Some text in the Modal..</p>
        </div>
      </div>

    </div>
  );
}

export default Home;